<template>
    <div class="footer">
        <div class="fmainbtn" :class="showNav?'active':''" @click="showNavs(this)" ref="fmainbtn"><button><img src="@/assets/image/header1.png" alt=""></button></div>
        <div class="fmnavs" :class="showNav?'active':''" :style="{'padding-top':theight+'px'}">
            <ul ref="fmnavbox">
                <li :class="[curIndex==1?'active':'',showStatus[0]?'showactive':'']">
                    <div class="fmnav1">
                        <router-link :to="{path:'/home'}" replace>首页</router-link >
                    </div>
                </li>
                <li :class="[curIndex==2?'active':'',showStatus[1]?'showactive':'']">
                    <div class="fmnav1">
                        <router-link :to="{path:'/articles'}" replace>文章</router-link >
                    </div>
                </li>
                <li :class="[curIndex==3?'active':'',showStatus[2]?'showactive':'']">
                    <div class="fmnav1">
                        <router-link :to="{path:'/apiclouds'}" replace>APICloud</router-link>
                    </div>
                </li>
                <li :class="[curIndex==4?'active':'',showStatus[3]?'showactive':'']">
                    <div class="fmnav1">
                        <router-link :to="{path:'/others'}" replace>其他</router-link>
                    </div>
                </li>
                <li :class="[curIndex==5?'active':'',showStatus[4]?'showactive':'']">
                    <div class="fmnav1">
                        <router-link :to="{path:'/mine'}" replace>个人中心</router-link>
                    </div>
                </li>
            </ul>
            <button class="refreshPage" @click="refreshFn" type="button"><img src="@/assets/image/refresh3.png" alt=""></button>
            <button class="quitApp" @click="quitFn" type="button"><img src="@/assets/image/quit2.png" alt=""></button>
        </div>
    </div>
</template>
<script>
export default {
    name: 'footers',
    data(){
        return{
            showNav:false,
            navNum:0,
            jlcurIndex:1,
            showStatus:[false,false,false,false,false]
        }
    },
    props: ['curIndex','theight'],
     mounted(){
         console.log(this.$refs.fmnavbox.children);
         this.navNum = this.$refs.fmnavbox.children.length;
         
     },
    methods:{
        curtips:function(tiptext){
            this.showMsg({text:tiptext+'----'+this.checkLine()});
        },
        showNavs(e){
            console.log(e);
            var curClass = this.$refs.fmainbtn.attributes.class.value;
            console.log(this.navNum);
            // console.log(curClass.ownerElement);
            // console.log(curClass.split(' '));
            this.showNav = !this.showNav;
            if(this.showNav){
                this.showfn(0);
            }else{
                this.showStatus = [false,false,false,false,false];
            }
        },
        showfn(curnum){
            var _this = this;
            this.$set(this.showStatus,curnum,true);
            if(curnum+1<this.showStatus.length){
                setTimeout(function(){
                    _this.showfn(curnum+1);
                },200)
            }
        },
        refreshFn(){
            window.location.reload();
        },
        quitFn(){
            // 退出应用
            // this.isApp?api.closeWidget():'';
            if(this.isApp){
                api.closeWidget({
                    id: 'A6036004057034', //这里是应用id
                    retData: {
                        name: 'closeWidget'
                    },
                    animation: {
                        type: 'flip',
                        subType: 'from_bottom',
                        duration: 500
                    }
                });
                // api.closeWidget({
                //     id: 'A6036004057034', //这里改成自己的应用ID
                //     retData: {name:'closeWidget'},
                //     silent:true
                // });
            }
        }
    },
    updated(){
        if(this.curIndex != this.jlcurIndex){
            this.showNav = false;
            this.jlcurIndex = this.curIndex;
            this.showStatus = [false,false,false,false,false];
        }
    },
    
}
</script>
<style scoped>
    .footer{}
    .footer button,
    .footer img{
        display: block;
    }
    .fmainbtn{
        position: fixed;
        bottom: 10px;
        left: 10px;
        z-index: 103;
    }
    .fmainbtn button{
        background-color: transparent;
        border: none;
    }
    .fmainbtn button img{
        width: 40px;
        height: 40px;
    }
    .fmnavs{
        position: fixed;
        top: 0;
        left: 0;
        width: 160px;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: 101;
        transition: all 0.3s;
        transform: translateX(-170px);
    }
    .fmnavs.active{
        transform: translateX(0);
    }
    .fmnavs a{
        color: white;
        padding: 10px 16px;
        border-left: 4px solid transparent;
    }
    .fmnavs li{
        background-color: rgba(0, 0, 0, 0.7);
        border-bottom: 1px solid rgb(53, 53, 53);
        transform: translateX(-170px);
        transition: all 0.3s;
    }
    .fmnavs li.showactive{
        transform: translateX(0px);
    }
    .fmnavs li.active a{
       border-color: rgb(0, 160, 233);
    }
    .refreshPage,
    .quitApp{
        position: absolute;
        bottom: 10px;
        left: -50px;
        transition: all 0.5s;
        transition-delay: 0.5s;
    }
    .fmnavs.active .refreshPage{
        left: 60px;
    }
    .refreshPage img,
    .quitApp img{
        width: 40px;
        height: 40px;
        border-radius: 50%;
        /* background-color: white; */
        border: none;
    }
    .quitApp{
        position: absolute;
        bottom: 10px;
        left: -50px;
        transition: all 0.5s;
        transition-delay: 0.7s;
    }
    
    .fmnavs.active .quitApp{
        left: 110px;
    }
</style>


